/* Media Query for Mobile Devices (e.g., up to 768px width) */
@media (max-width: 768px) {
  .coursesCard {
    padding: 10px 0px;

    
  }
  .coursesCard .items {
    padding: 15px; /* Adjust padding as needed for mobile layout */
  }
  .coursesCard .img {
    width: 50px;
    height: 50px; /* Adjust size for mobile layout */
    padding: 10px;
  }
  .coursesCard .text h1 {
    font-size: 20px; /* Adjust font size for mobile layout */
    line-height: 30px;
  }
  .coursesCard .rate i {
    font-size: 10px; /* Adjust font size for mobile layout */
    margin-right: 3px;
  }
  .coursesCard .details .dimg img {
    width: 40px;
    height: 40px; /* Adjust size for mobile layout */
    margin-right: 10px;
  }
  .coursesCard .details span {
    font-size: 12px; /* Adjust font size for mobile layout */
  }
  .coursesCard .price {
    margin: 10px 0; /* Adjust margin as needed for mobile layout */
    padding: 5px;
  }
  .coursesCard h3 {
    font-size: 18px; /* Adjust font size for mobile layout */
  }

  /* Additional mobile-specific styles and adjustments can be made here */
  .online .box {
    padding: 20px 10px; /* Adjust padding as needed for mobile layout */

  }
  .online .img {
    width: 60px;
    height: 60px; /* Adjust size for mobile layout */
    padding: 10px;
  }
  .online h1 {
    font-size: 16px; /* Adjust font size for mobile layout */
    margin: 10px 0;
    line-height: 24px;
  }
  .online span {
    font-size: 12px; /* Adjust font size for mobile layout */
    padding: 3px 10px;
  }
  
  .online{
    margin: 0px;
    

  }
}
