/* Media Query for Mobile Devices (e.g., up to 768px width) */
@media (max-width: 768px) {
  .container {
    max-width: 100%; 
    padding-top: 5px;
    margin-left: 10px;
    
  }
  .newcontainer{
    width: 95%;
    height: 250px;
    background-color: #f80000;
    align-items:flex-start;
    margin-left: 10px;
    
  }
  /* -----------------button section----------- */
  .primary-btn {
    width: 180px; 
    height: 50px; 
    padding: 5px 10px;
    text-align:center;
    font-size: 11px;  
  }
  .secondary-btn{
    width: 160px; 
    height: 50px; 
    padding: 5px 10px;
    text-align:center;
    font-size: 11px; 
  }
  /* ---------------------------------------------- */

  .grid,
  .grid2,
  .grid3 {
    grid-template-columns: 1fr; 
    grid-gap: 10px; /* Adjust grid gap for mobile layout */
    padding: 10px;
  }
  #heading{
    
    width: 100%;
    margin-left: 10px;
    text-align:start;
    
    
  }
  .outline-btn{
    margin-top: 20px;
  }
  
  
  
  
}
